import axios from 'axios'
import { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import '../../styles/dashboard.scss'
import UserPool from '../../UserPool'
import Context from '../../Context'

const DashboardHome = () => {

    const { token } = useContext(Context)
    const [username, setusername] = useState("")
    const [status, setstatus] = useState(false)
    const [loading, setloading] = useState(true)
    const [data, setdata] = useState({Pickup: "", Destination: ""})
    const [rides, setrides] = useState([])
    const [currride, setcurrride] = useState({})

    // Fetch current user email and rides
    useEffect(() => {
        const user = UserPool.getCurrentUser()
        if (user) {
            user.getSession((err, session) => {
                if (err) {
                    setstatus(false)
                    setTimeout(() => {
                        setloading(false)
                    }, 1000)
                }
                else if (session) {
                    setstatus(true)
                    user.getUserAttributes((e, i) => {
                        i.map(attr => {
                            if (attr.Name === "email") {
                                setusername(attr.Value)
                                setloading(false)
                            }
                        })
                    })
                }
            })
        }
        setTimeout(() => {
            setloading(false)
        }, 3000)
        fetchRides()
    }, [])

    useEffect(() => {
        if(rides.length > 0) {
            const currTime = parseInt(Date.now()/1000)
            const ongoingRides = rides.filter(ride => (
                ride.StartTime < currTime) &&
                ride.EndTime > currTime
            )
            console.log(ongoingRides)
            setcurrride(ongoingRides[0])
        }
    }, [rides])

    const fetchRides = async e => {
        try {
            let res = await axios.get(
                `https://2mxr5ww0k0.execute-api.ap-northeast-1.amazonaws.com/prod/rides`,
                {
                    headers: { Authorization: token.IdToken }
                }
            )
            const rideData = res.data
            rideData.sort((a,b) => a.StartTime-b.StartTime)
            setrides(rideData)
        }
        catch (err) {
            console.log(err)
        }
    }

    const logout = e => {
        const user = UserPool.getCurrentUser()
        if (user) {
            user.signOut()
            setusername('')
        }
    }

    const requestRide = async e => {
        try {
            const {Pickup, Destination} = data
            console.log(Pickup, Destination)
            if(!Pickup || !Destination) {
                alert('Invalid Input Fields')
                return;
            }
            let res = await axios.post(
                "https://2mxr5ww0k0.execute-api.ap-northeast-1.amazonaws.com/prod/ride",
                {
                    Pickup,
                    Destination
                },
                {
                    headers: { Authorization: token.IdToken }
                }
            )
            setcurrride(res.data)
        }
        catch (err) {
            console.log(err)
        }
    }

    const parseDate = dateseconds => {
        const months = ['Jan','Feb','mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec']
        var date = new Date(dateseconds);
        let day = date.getDate()
        let hour = date.getHours()
        let minute = date.getMinutes()
        let ampm = hour >= 12 ? "pm" : "am"
        return `${day < 10 ? "0"+day : day} ${months[(date.getMonth()+1)]} ${date.getFullYear()} ${hour < 10 ? "0"+hour : hour}:${minute < 10 ? "0"+minute : minute} ${ampm}`
    }

    const handleChange = e => setdata({...data, [e.target.name]: e.target.value})

    return (
        <div className='dashboard'>
            {loading === false ?
                username ?
                    (<div>
                        <nav>
                            <p>Hello, <span>{username}</span></p>
                            <button onClick={logout}>Logout</button>
                        </nav>
                        <main>
                            {currride && currride?.UserId ? 
                            <div className='currRide'>
                                <h1>On Going Ride:</h1>
                                <p className='Pickup'><span>Pickup:</span> {currride.Pickup}</p>
                                <p className='Destination'><span>Destination:</span> {currride?.Destination}</p>
                                <p className='Driver'><span>Driver Name:</span> {currride?.DriverfirstName} {currride?.DriverlastName}</p>

                                <p><span>Start Time:</span>{parseDate(currride.StartTime*1000)}</p>
                                <p><span>End Time:</span>{parseDate(currride.EndTime*1000)}</p>
                            </div> : 
                            <div className='bookRide'>
                                <h1>Book a new Unicorn</h1>
                                <div className='form'>
                                    <input name="Pickup" onChange={handleChange} value={data.Pickup} placeholder='Pickup Location' />
                                    <input name="Destination" onChange={handleChange} value={data.Destination} placeholder='Destination' />
                                    <button onClick={requestRide}>Request Ride</button>
                                </div>
                            </div>
                            }
                            <div className='allRides'>
                                <div className='head'>
                                    <h1>All Rides</h1>
                                    <button onClick={fetchRides}>Refresh</button>
                                </div>
                                {rides && rides.length > 0 ? <ul>
                                    <li className='header'>
                                                <p>Pickup</p>
                                                <p>Destination</p>
                                                <p>Booked At</p>
                                                <p>Reached At</p>
                                            </li>
                                        {rides.map((i,index) => i.EndTime <= parseInt(Date.now()/1000) ? (
                                            <li key={index}>
                                                <p><span>{i.Pickup}</span></p>
                                                <p><span>{i.Destination}</span></p>
                                                <p><span>{new Date(i.StartTime*1000).toLocaleString()}</span></p>
                                                <p><span>{new Date(i.EndTime*1000).toLocaleString()}</span></p>
                                            </li>
                                        ) : null)}
                                    </ul> : null}
                            </div>
                        </main>
                    </div>)
                    :
                    (<div className='authreq'>
                        <h1>You need to Login!</h1>
                        <Link to="/">Return to Home</Link>
                    </div>) :
                (<div className='loading'><h2>Loading...</h2></div>)
            }

        </div>
    )
}

export default DashboardHome