import { useContext, useEffect, useState } from "react"
import { CognitoUser, AuthenticationDetails } from 'amazon-cognito-identity-js'
import UserPool from '../UserPool'
import { useNavigate } from "react-router-dom"
import Context from '../Context'

const SignIn = ({ toggleSignUp }) => {

    const navigate = useNavigate()
    const { token, settoken } = useContext(Context)

    const [logindata, setlogindata] = useState({
        email: "",
        password: ""
    })
    const handleLoginData = e => {
        setlogindata({ ...logindata, [e.target.name]: e.target.value })
    }

    useEffect(() => {
        console.log(token)
    }, [token])

    const onSubmit = e => {
        e.preventDefault()

        const user = new CognitoUser({
            Username: logindata.email,
            Pool: UserPool,
        })

        const authDetails = new AuthenticationDetails({
            Username: logindata.email,
            Password: logindata.password
        })

        user.authenticateUser(authDetails, {
            onSuccess: (data) => {
                settoken({
                    IdToken: data.getIdToken().getJwtToken(),
                    AccessToken: data.getAccessToken().getJwtToken(),
                })
                console.log('OnSuccess: ', data)
                navigate('/dashboard')
            },
            onFailure: (err) => {
                alert(err.message)
            },
            newPasswordRequired: (data) => {
                console.log('NewPasswordRequired: ', data)
            }
        })

    }

    return (
        <div className="actions">
            <form onSubmit={onSubmit}>
                <h2>Sign In Here!</h2>
                <fieldset className="field">
                    <legend>Email</legend>
                    <input name="email" placeholder="Enter Your Email" value={logindata.email} onChange={handleLoginData} type="email" />
                </fieldset>
                <fieldset className="field">
                    <legend>Password</legend>
                    <input name="password" placeholder="Enter Your Password" value={logindata.password} onChange={handleLoginData} type="password" />
                </fieldset>
                <input type="submit" value="Sign In" />
            </form>
            <p>
                Do not have an account?
                <span onClick={toggleSignUp}>Sign Up</span>
            </p>
        </div>
    )
}

export default SignIn