import { useState } from "react"
import { CognitoUserAttribute } from 'amazon-cognito-identity-js';
import UserPool from '../UserPool'

const SignUp = ({ toggleSignUp }) => {

    const [registerdata, setregisterdata] = useState({
        firstName: "",
        lastName: "",
        email: "",
        password: ""
    })
    const handleRegisterData = e => {
        setregisterdata({ ...registerdata, [e.target.name]: e.target.value })
    }

    const handleSubmit = async e => {
        e.preventDefault()

        const { email, password, firstName, lastName, } = registerdata
        const attributeList = []
        attributeList.push(new CognitoUserAttribute({ Name: 'name', Value: firstName }))
        attributeList.push(new CognitoUserAttribute({ Name: 'family_name', Value: lastName }))
        attributeList.push(new CognitoUserAttribute({ Name: 'custom:category', Value: "0" }))
        UserPool.signUp(email, password, attributeList, null, (err, data) => {
            if (err) {
                alert(err.message)
            }
            if (data) {
                console.log(data.user)
                alert('You can now sign in!')
                // setUser({ email: data.user.username })
            }
        })
    }

    return (
        <div className="actions">
            <form onSubmit={handleSubmit}>
                <h2>Sign Up Here!</h2>
                <div className="field">
                    <fieldset className="firstName">
                        <legend>First Name</legend>
                        <input name="firstName" placeholder="Enter Your First Name" value={registerdata.firstName} onChange={handleRegisterData} type="text" />
                    </fieldset>
                    <fieldset className="lastName">
                        <legend>Last Name</legend>
                        <input name="lastName" placeholder="Enter Your Last Name" value={registerdata.lastName} onChange={handleRegisterData} type="text" />
                    </fieldset>
                </div>

                <fieldset>
                    <legend>Email</legend>
                    <input name="email" placeholder="Enter Your Email" value={registerdata.email} onChange={handleRegisterData} type="email" />
                </fieldset>
                <fieldset>
                    <legend>Password</legend>
                    <input name="password" placeholder="Enter Your Password" value={registerdata.password} onChange={handleRegisterData} type="password" />
                </fieldset>
                <input type="submit" value="Sign Up" />
            </form>
            <p>
                Already have an account?
                <span onClick={toggleSignUp}>Sign In</span>
            </p>
        </div>
    )
}

export default SignUp